.is-dark-mode {
  --header: rgb(26 26 26);
  --search: #343434;
  --paginate-active: #343434;
  --paginate-hover: #363636;
  --app-background: rgb(34, 34, 34);
  --pokemon-card: #1c1c1c;
  --pokemon-card-border: #6060603d;
  --pokemon-view-card: #202020;
  --pokemon-view: #2a2a2a;
  --color-primary: #fff;
  --pokemon-svg: 0.1;
  /* Switch */
  --switchSpinner: #282943;
  --switch: #3e3e3e;
}
.is-light-mode {
  --header: #fff;
  --search: #78b2ff;
  --paginate-active: #78b2ff;
  --paginate-hover: #78b2ff;
  --app-background: #fff;
  --pokemon-card: #f9fcff;
  --pokemon-card-border: #ebebeb;
  --pokemon-view-card: #fff;
  --pokemon-view: #fff;
  --color-primary: #000;
  --pokemon-svg: 1;
  /* Switch */
  --switchSpinner: white;
  --switch: #b1b4be;
}
