.Header {
  background: var(--header);
  border-bottom: 1px solid var(--pokemon-card-border);
  position: sticky;
  top: 0;
  z-index: 9;
}
.Navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1300px;
  margin: auto;
  padding: 20px;
}

.Logo img {
  width: 150px;
  display: block;
}
.Content {
  display: flex;
  align-items: center;
  grid-gap: 20px;
}
