.Form-Search {
  width: 100%;
  margin-block-end: 20px;
}
.Form-Search-Title {
  margin-block-start: 20px;
  margin-block-end: 20px;
  font-size: 20px;
  font-weight: 500;
  color: var(--color-primary);
}
.Form-Search-Input {
  display: flex;
  border-radius: 30px;
  border: 1px solid var(--pokemon-card-border);
  overflow: hidden;
}
.Form-Search-Input input {
width: 100%;
  border: none;
  outline: none;
  padding: 5px;
  padding-inline-start: 20px;
  padding-inline-end: 20px;
  font-size: 15px;
  color: var(--color-primary);
  background-color: var(--pokemon-card);
}
.Form-Search-Input input[type="number"]::-webkit-inner-spin-button,
.Form-Search-Input input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.Search-Pokemon {
  padding: 20px;
  border-radius: 20px;
  background-color: #78b2ff;
  height: 100vh;
}
.Form-Search-Input button {
  cursor: pointer;
  border: none;
  outline: none;
}
.Form-Search-Input .Icon {
  font-weight: 500;
  padding: 15px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-primary);
  background: var(--search);
  transition: 300ms;
}
.Form-Search-Input .Icon:hover {
  color: var(--color-primary);
  background: var(--switch);
}
