.View {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 0px;
  border: 1px solid var(--pokemon-card-border);
  background-color: var(--pokemon-view-card);
  border-radius: 20px;
}
@media (min-width: 300px) {
  .View {
    order: 1;
  }
}
@media (min-width: 768px) {
  .View {
    order: 2;
  }
}
.Pokemon-View {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 20px;
}
.Pokemon-View h2 {
  color: var(--color-primary);
  font-weight: 500;
  font-size: 25px;
}
