progress {
  -webkit-appearance: none;
  border-radius: 20px;
  /* height: 12px; */
  height: 9px;
  overflow: hidden;
}

progress::-webkit-progress-bar-value {
  -webkit-appearance: none;
}
progress::-webkit-progress-value {
  background: #0080ff;
  border-radius: 20px;
}

.Defense progress::-webkit-progress-value {
  background: rgb(36, 212, 89);
  border-radius: 20px;
}
.Attack progress::-webkit-progress-value {
  background: #0080ff;
  border-radius: 20px;
}
progress::-webkit-progress-bar {
  background: rgb(223, 223, 223);
}
