.Pokelist {
  display: grid;
  width: 100%;
  grid-gap: 20px;
  margin: auto;
}
.Pokelist-Container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr 1fr;
}

@media (min-width: 300px) {
  .Pokelist {
    grid-template-columns: 1fr;
  }
  .Pokelist-Container {
    order: 2;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .Pokelist {
    grid-template-columns: 2fr 1fr;
  }
  .Pokelist-Container {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1024px) {
  .Pokelist-Container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
