.paginationBttns {
  width: 100%;
  height: 40px;
  margin: auto;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block-start: 50px;
  margin-block-end: 50px;
}
.paginationBttns a {
  cursor: pointer;
  text-decoration: none;
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid rgb(14 14 14 / 12%);
  transition: 150ms;
  color: var(--color-primary);
  background-color: var(--pokemon-card);
}
.paginationBttns a:hover {
  color: white;
  background: var(--paginate-hover);
}
.paginationActive a {
  color: white;
  background: var(--paginate-active);
}
.paginationDisabled a {
  /* background-color: rgb(14 14 14 / 12%); */
  border: 1px solid rgb(14 14 14 / 12%);
}
