.Pokemon-Card {
  display: flex;
  grid-gap: 5px;
  flex-direction: column;
  padding: 20px;
  width: 80%;
  border-radius: 20px;
  border: 1px solid var(--pokemon-card-border);
  background-color: var(--pokemon-view);
}
.Pokemon-Card h3 {
  display: flex;
  align-items: center;
  text-transform: capitalize;
  grid-gap: 10px;
  color: var(--color-primary);
  font-weight: 600;
}
.Pokemon-Card h3 span {
  color: var(--color-primary);
  font-size: 18px;
  font-weight: 600;
  border: 1px solid var(--pokemon-card-border);
  padding: 2px 10px;
  border-radius: 30px;
}
.Pokemon-Card h3 p {
  font-size: 18px;
  font-weight: 500;
}
.Pokemon-Card-Img {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-block-start: 10px;
  margin-block-end: 10px;
}
.Pokemon-Card-Picture {
  position: absolute;
}
.Pokemon-Card-Background {
  opacity: var(--pokemon-svg);
  width: 120px;
}
.Pokemon-Card img {
  width: 150px;
  display: block;
  margin: auto;
}
