.Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  padding: 5px;
  color: var(--color-primary);
  cursor: pointer;
  background: var(--pokemon-card);
  border: 1px solid var(--pokemon-card-border);
  border-radius: 20px;
}
.Card-Title {
  font-size: 18px;
  /* font-weight: 500; */
  font-weight: 500;
  text-transform: capitalize;
  text-align: center;
}
.Card-Img {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.Poke-Img {
  position: absolute;
}
.Poke-Back {
  opacity: var(--pokemon-svg);
  width: 120px;
}
.Card-Type {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  text-transform: capitalize;
}
.Card-Content {
  margin-block-start: 5px;
  margin-block-end: 15px;
  display: flex;
  grid-gap: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
